<template>
  <!-- 这里是登录页 -->
  <div>
    <van-nav-bar left-text="回到首页" @click-left="onClickLeft" left-arrow>
      <template #right>
        <div @click="onClickRight">
          <span v-show="isLog">注册</span>
          <span v-show="!isLog">登录</span>
        </div>
      </template>
    </van-nav-bar>

    <div class="main">
      <div class="title">
        让我们开始品尝
        <br />咖啡吧！☕
      </div>

      <div class="input">
        <!-- 登录页 -->
        <div v-show="isLog">
          <van-form @submit="onSubmit" @failed="onFailed">
            <van-field
              v-model="userInfo.phonenum"
              label-width="20px"
              name="手机号"
              label="手机号"
              placeholder="手机号"
              autocomplete="false"
              :rules="[{pattern:userPattern.phonePattern, required: true, message: '请填写正确手机号' }]"
            />
            <van-field
              v-model="userInfo.password"
              type="password"
              name="密码"
              label="密码"
              placeholder="请填写六位数密码"
              autocomplete="false"
              :rules="[{pattern:userPattern.pwdPattern,required: true, message: '请填写六位数密码' }]"
            >
              <template #right-icon>
                <van-switch
                  active-color="#7f5839"
                  @click="btnChange"
                  size="20px"
                  v-model="switchChecked"
                />
              </template>
            </van-field>
            <div style="margin: 16px;">
              <van-button round block type="info" native-type="submit">登录</van-button>
            </div>
          </van-form>
        </div>
        <!-- 注册页 -->
        <div v-show="!isLog">
          <van-form @submit="onSubmit" @failed="onFailed">
            <van-field
              v-model="userInfo.phonenum"
              name="手机号"
              label="手机号"
              placeholder="手机号"
              autocomplete="false"
              :rules="[{pattern:userPattern.phonePattern, required: true, message: '请填写正确手机号' }]"
            />

            <van-field
              v-model="userInfo.username"
              name="用户名"
              label="用户名"
              placeholder="请填写六位汉字字母组合"
              autocomplete="false"
              :rules="[{pattern:userPattern.userPattern, required: true, message: '六位汉字字母组合' }]"
            />
            <van-field
              v-model="userInfo.password"
              type="password"
              name="密码"
              label="密码"
              placeholder="请填写六位数密码"
              autocomplete="false"
              :rules="[{pattern:userPattern.pwdPattern,required: true, message: '请填写六位数密码' }]"
            >
              <template #right-icon>
                <van-switch
                  active-color="#7f5839"
                  @click="btnChange"
                  size="20px"
                  v-model="switchChecked"
                />
              </template>
            </van-field>
            <!-- 
            <van-field
              v-model="validnum"
              name="验证码"
              label="验证码"
              placeholder="验证码"
              :rules="[{required: true, message: '请填写验证码' }]"
            >
              <template #button>
                <van-button
                  @click="getValidnum"
                  :disabled="isDisabled"
                  native-type="button"
                  size="small"
                  color="#7f5839"
                  :text="validText"
                ></van-button>
              </template>
            </van-field>-->
            <div style="margin: 16px;">
              <van-button round block type="info" native-type="submit">注册</van-button>
            </div>
          </van-form>
        </div>
        <div class="taggle" v-show="isLog">
          <span @click="onClickRight">前往注册</span>
          <span @click="forgetPwd" class="forget-pwd">忘记密码</span>
        </div>
        <div class="taggle" v-show="!isLog">
          <span @click="onClickRight">已有账号,前往登录</span>
        </div>
      </div>
    </div>

    <div class="bottom">
      <p>第三方登录</p>
      <div class="bot-list">
        <div class="bot-item iconfont icon-weixin"></div>
        <div class="bot-item iconfont icon-weibo"></div>
        <div class="bot-item iconfont icon-QQ"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Api from "../api/api";
import Util from "../util/utils";
export default {
  data() {
    return {
      isLog: true,
      switchChecked: false,
      isDisabled: false
      // validText: "获取验证码",
      // validnum: ""
    };
  },
  created() {
    // 看一下用户是否已经有cookies
    let token = Util.getCookies(this);

    //去后台验证一下是否是有效的token 待定
    if (token) {
      // 改变登录状态
      this.$store.commit("changeLogin", { bool: true });

      // 跳转首页
      this.$router.push({ name: "Home" });
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.loginModule.userInfo;
    },
    userPattern() {
      return this.$store.state.loginModule.userPattern;
    }
  },
  mounted() {
    // 修改输入框的宽度
    let element = document.querySelectorAll(".van-cell__title");
    for (let i = 0; i < element.length; i++) {
      element[i].style.flex = ".3";
    }
  },

  methods: {
    onClickLeft() {
      this.$router.push({ name: "Home" });
    },
    onClickRight() {
      // 切换的时候需要进行清空
      this.isLog = !this.isLog;
      this.$store.commit("loginModule/clearUserInfo");
      this.switchChecked = false;
    },
    // 显示或者隐藏密码
    btnChange(e) {
      let father = e.target.parentNode.parentNode.parentNode;
      let pwd = father.querySelector("input");
      if (!this.switchChecked) {
        pwd.type = "text";
      } else {
        pwd.type = "password";
      }
    },
    onSubmit() {
      // 格式正确，能正确提交

      if (this.isLog) {
        // 格式正确，能正确提交
        this.$toast.loading({
          duration: 0,
          message: "登录中...",
          // 禁止背景点击
          forbidClick: true
        });
        //如果这是在登录界面的话
        Api.login({
          phone: this.userInfo.phonenum,
          password: this.userInfo.password
        }).then(res => {
          // 再清空信息
          this.$store.commit("loginModule/clearUserInfo");
          // 清除动画
          this.$toast.clear();
          if (res.data.code == 200) {
            this.$toast("登录成功");

            // 设置cookies过期时间
            let time = 60 * 60 * 24 * 30;

            let tokenData = res.data.token.split(".");

            for (let i = 0; i < tokenData.length; i++) {
              this.$cookies.set("m" + i, tokenData[i], time);
            }
            // 改变登录状态
            this.$store.commit("changeLogin", { bool: true });

            // 跳转首页
            this.$router.push({ name: "Home" });
          } else {
            // 输出提示
            this.$toast(res.data.msg);
          }
        });
      } else {
        this.$toast.loading({
          duration: 0,
          message: "注册中...",
          // 禁止背景点击
          forbidClick: true
        });
        //如果是在注册界面的话
        Api.register({
          phone: this.userInfo.phonenum,
          password: this.userInfo.password,
          nickName: this.userInfo.username
        }).then(res => {
          // 清除动画
          this.$toast.clear();
          // 再清空信息
          this.$store.commit("loginModule/clearUserInfo");
          // 注册成功
          if (res.data.code == 100) {
            // 去登录页面
            this.onClickRight();
          }
          // 输出提示
          this.$toast(res.data.msg);
        });
      }
    },
    onFailed() {
      this.$toast("请输入正确信息");
    },
    forgetPwd() {
      this.$router.push({ name: "Forgetpwd" });
    }
  }
};
</script>

<style lang="scss" scoped>
.forget-pwd {
  position: absolute !important;
  right: 10px;
  bottom: 0;
}

/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}
.input {
  .van-button--info {
    background-color: #7f5839;
    border: 1px solid #7f5839;
    .van-button__text {
      font-size: 18px;
      font-weight: 600;
      color: #fff;
    }
  }
}
.taggle {
  position: relative;
  text-align: center;
  color: #999;
  span {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      background-color: #999;
      height: 1px;
      width: 90%;
      left: 5%;
      bottom: -1px;
    }
  }
}
.main {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.title {
  margin: 0px 20px 20px;
  line-height: 40px;
  font-size: 28px;
  font-weight: 600;
}

.bottom {
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  text-align: center;
  p {
    color: #8e8e8e;
    position: relative;
    bottom: -6px;
  }
  .bot-list {
    display: flex;
    .bot-item {
      width: calc(100% / 3);
      height: 50px;
      line-height: 50px;
      font-size: 20px;
      background-color: #fbfbfb;
      color: #8e8e8e;
    }
  }
}
</style>